import { Helmet } from 'react-helmet';
import React from 'react';
import { Form, FormGroup, Label, Input, Button, Alert } from 'reactstrap';
import axios from 'axios';

import './Contact.scss';

const Contact = (props) => {

  // useState
  const [ formData, $formData ] = React.useState({ name: '', email: '', message: '' });
  const [ formErr, $formErr ] = React.useState({ name: '', email: '', message: '' });
  const [ misc, $misc ] = React.useState({ successAlert: '', errorAlert: '' });

  const [ canFetch, $canFetch ] = React.useState(false);

  //useEffect
  React.useEffect(() => {
    // window.scrollTo(0, 0);

    function valTable(data, errs) {
      let can = true;

      Object.values(data).forEach((val) => {
        val === '' && (can = false);
      });

      Object.values(errs).forEach((val) => {
        val.length && (can = false);
      });

      return can;
    }

    valTable(formData, formErr) ? $canFetch(true) : $canFetch(false);

  }, [ formData, formErr ]);

  // === Custom ===

  // onChange
  const onChange = (e) => {
    e.preventDefault();

    const { name, value } = e.target;
    $formData({ ...formData, [ name ]: value });
    $misc({ ...misc, successAlert: '', errorAlert: '' });

    let errs = formErr;

    switch (name) {
      case 'name':
        if (value === '') {
          errs.name = 'Stranger, what I would call you?';
        } else if (!value.match(/^[a-zA-Z-.:]+\s?[a-zA-Z-.:]+$/)) {
          errs.name = 'Invalid Name';
        } else {
          errs.name = '';
        }
        break;

      case 'email':
        if (value === '') {
          errs.email = 'How I am gonna reply to you?';
          // eslint-disable-next-line
        } else if (!value.match(/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/)) {
          errs.email = 'Invalid E-mail';
        } else {
          errs.email = '';
        }
        break;

      case 'message':
        if (value === '') {
          errs.message = 'What is the point all of this then?';
        } else if (value.match(/^[ ]+$/) || !value.match(/^[a-zA-Z0-9-,.!?:#()]+( [a-zA-Z0-9-,.!?:#()]+)*$/)) {
          errs.message = 'Invalid Message | unexpected spaces or characters';
        } else {
          errs.message = '';
        }
        break;

      default:
        break;
    }


  };

  // onSubmit
  const onSubmit = (e) => {
    e.preventDefault();

    axios.post('/', formData)
      .then((resp) => {
        if (resp.status === 200 && resp.data.msg === 'msG_SeNt') {
          $formData({ ...formData, name: '', email: '', message: '' });
          $misc({ ...misc, successAlert: resp.data.message });
        }
      })
      .catch((err) => {

        if (err.message === 'Network Error') {
          $misc({ ...misc, errorAlert: err.message });
        } else {
          $formErr({
            ...formErr,
            name: err.response.data.formErr.name,
            email: err.response.data.formErr.email,
            message: err.response.data.formErr.message
          });

          $misc({ ...misc, errorAlert: err.response.data.message });
        }
      });
  };

  return (
    <React.Fragment>
      <Helmet>
        <title>AharrFaris || Contact</title>
        <meta name="developed by" content="Habibullah" />
        <meta name="design and developed by" content="Habibullah" />
        <meta name="portfolio" content="Habibullah" />
        <meta name="prortfolio website" content="Habibullah" />
        <meta name="projects" content="Habibullah" />
        <meta name="contact" content="Habibullah" />
        <meta name="react.js developer" content="Habibullah" />
        <meta name="react developer" content="Habibullah" />
        <meta name="node.js node developer" content="Habibullah" />
        <meta name="node developer" content="Habibullah" />
        <meta name="fullstack developer" content="Habibullah" />
        <meta name="web developer" content="Habibullah" />
        <meta name="react.js developer in Bangladesh" content="Habibullah" />
        <meta name="react developer in Bangladesh" content="Habibullah" />
        <meta name="node.js node developer in Bangladesh" content="Habibullah" />
        <meta name="node developer in Bangladesh" content="Habibullah" />
        <meta name="fullstack developer in Bangladesh" content="Habibullah" />
        <meta name="web developer in Bangladesh" content="Habibullah" />
        <meta name="react.js vue.js node.js gatsby javascript developer" content="Habibullah" />
        <meta name="react vue node gatsby javascript developer" content="Habibullah" />
        <meta name="javascript developer" content="Habibullah" />
        <meta name="javascript developer in Bangladesh" content="Habibullah" />

        <meta name="developed by" content="AharrFaris" />
        <meta name="design and developed by" content="AharrFaris" />
        <meta name="portfolio" content="AharrFaris" />
        <meta name="prortfolio website" content="AharrFaris" />
        <meta name="projects" content="AharrFaris" />
        <meta name="contact" content="AharrFaris" />
        <meta name="react.js developer" content="AharrFaris" />
        <meta name="react developer" content="AharrFaris" />
        <meta name="node.js node developer" content="AharrFaris" />
        <meta name="node developer" content="AharrFaris" />
        <meta name="fullstack developer" content="AharrFaris" />
        <meta name="web developer" content="AharrFaris" />
        <meta name="react.js developer in Bangladesh" content="AharrFaris" />
        <meta name="react developer in Bangladesh" content="AharrFaris" />
        <meta name="node.js node developer in Bangladesh" content="AharrFaris" />
        <meta name="node developer in Bangladesh" content="AharrFaris" />
        <meta name="fullstack developer in Bangladesh" content="AharrFaris" />
        <meta name="web developer in Bangladesh" content="AharrFaris" />
        <meta name="react.js vue.js node.js gatsby javascript developer" content="AharrFaris" />
        <meta name="react vue node gatsby javascript developer" content="AharrFaris" />
        <meta name="javascript developer" content="AharrFaris" />
        <meta name="javascript developer in Bangladesh" content="AharrFaris" />


        <meta name="developed by" content="aharrfaris" />
        <meta name="design and developed by" content="aharrfaris" />
        <meta name="portfolio" content="aharrfaris" />
        <meta name="prortfolio website" content="aharrfaris" />
        <meta name="projects" content="aharrfaris" />
        <meta name="contact" content="aharrfaris" />
        <meta name="react.js developer" content="aharrfaris" />
        <meta name="react developer" content="aharrfaris" />
        <meta name="node.js node developer" content="aharrfaris" />
        <meta name="node developer" content="aharrfaris" />
        <meta name="fullstack developer" content="aharrfaris" />
        <meta name="web developer" content="aharrfaris" />
        <meta name="react.js developer in Bangladesh" content="aharrfaris" />
        <meta name="react developer in Bangladesh" content="aharrfaris" />
        <meta name="node.js node developer in Bangladesh" content="aharrfaris" />
        <meta name="node developer in Bangladesh" content="aharrfaris" />
        <meta name="fullstack developer in Bangladesh" content="aharrfaris" />
        <meta name="web developer in Bangladesh" content="aharrfaris" />
        <meta name="react.js vue.js node.js gatsby javascript developer" content="aharrfaris" />
        <meta name="react vue node gatsby javascript developer" content="aharrfaris" />
        <meta name="javascript developer" content="aharrfaris" />
        <meta name="javascript developer in Bangladesh" content="aharrfaris" />
      </Helmet>

      <section id="Contact">

        <Form onSubmit={onSubmit}>

          {
            misc.errorAlert &&
            <Alert color="danger">{misc.errorAlert}</Alert>
          }

          {
            misc.successAlert &&
            <Alert color="success">{misc.successAlert}</Alert>
          }

          <FormGroup>
            <Label for="name">
              Name:
              {formErr.name && <span className="text-danger">{formErr.name}</span>}
            </Label>
            <Input
              type="text"
              name="name"
              id="name"
              placeholder="Name"
              className={formErr.name ? "input-err" : ""}
              onChange={onChange}
              value={formData.name}
            />
          </FormGroup>

          <FormGroup>
            <Label for="email">
              E-mail:
              {formErr.email && <span className="text-danger">{formErr.email}</span>}
            </Label>
            <Input
              type="email"
              name="email"
              id="email"
              placeholder="E-mail"
              className={formErr.email ? "input-err" : ""}
              onChange={onChange}
              value={formData.email}
            />
          </FormGroup>

          <FormGroup>
            <Label for="message">Message:
              {formErr.message && <span className="text-danger">{formErr.message}</span>}
            </Label>
            <Input
              type="textarea"
              name="message"
              id="message"
              placeholder="What is it About?"
              className={formErr.message ? "input-err" : ""}
              onChange={onChange}
              value={formData.message}
            />
          </FormGroup>

          <Button
            block
            onClick={onSubmit}
            className={!canFetch ? "btn-err" : ""}
            disabled={!canFetch}
          >{canFetch ? "Fetch!" : "..."}</Button>
        </Form>
      </section>

    </React.Fragment >
  );
};

export default Contact;